/* test */

.animated-border-button {
  background-color: #334179;
  border: 0.2px solid white !important;

  color: #ffffff;
  outline: none;
  padding: 10px 10px 10px;
  position: relative;
}

.animated-border-button:before,
.animated-border-button:after {
  border: 1 solid transparent;
  transition: all 0.3s;
  content: "";
  height: 0;
  position: absolute;
  width: 2px;
}

.animated-border-button:before {
  border-top: 2px solid rgba(26, 237, 96, 0.38);
  right: 0;
  top: -4px;
}

.animated-border-button:after {
  border-bottom: 2px solid rgba(26, 237, 96, 0.38);
  bottom: -4px;
  left: 0;
}

.animated-border-button:hover:before,
.animated-border-button:hover:after {
  width: 100%;
}

